.glass {
  background: linear-gradient(
    218.99deg,
    rgba(255, 255, 255, 0.2) 4.42%,
    rgba(255, 255, 255, 0) 85.6%
  );
  backdrop-filter: blur(5px);
  /* Note: backdrop-filter has minimal browser support */

  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}